import { createApp } from "vue";
// import { createHead } from '@vueuse/head'

import App from "./components/App.vue";
import { store, key } from '@/store/index'

import {
    Checkbox, Card, Tag, Tree, Table, Select, Button, Form, InputNumber, Menu, Dropdown,Popover, Pagination, Tooltip,Statistic,Col,Row,Space
} from "ant-design-vue";

createApp(App)
    .use(store, key)
    .use(Checkbox).use(Card).use(Tag).use(Tree).use(Table).use(Select).use(Button).use(Form).use(InputNumber).use(Menu).use(Dropdown).use(Popover).use(Pagination).use(Tooltip).use(Statistic).use(Col).use(Row).use(Space)
    .mount("#app");
