let dataDic :{ [key: string]: {}[]; }= {}


for (let i=1;i<dataKey.length; ++i){
  feature_value = Object.values(FullFeatureData[dataKey[i]]);
  let data = [{
      key: '1',
      feature: feature_name[0],
      value: feature_value[0],
      explantion: 'The JPEG compression ratio of the H channel when keeping high quality.',
  }];

  for (let i=1;i<feature_name.length;++i){
      let num = i+1
      let temp = {
          key: num.toString(),
          feature: feature_name[i],
          value: feature_value[i],
          explantion: FeatureExplan[i]["explanation"],        
      }
      data.push(temp)
  }
  let dicKey = dataKey[i] as string;
  dataDic[dicKey] = data;
}
