import { InjectionKey } from 'vue'
import { createStore, useStore as baseUseStore, Store } from 'vuex'
import * as dataService from "@/services/dataService";
import { TypeOfList } from "underscore";

// init default 
import { Typing } from "@/utils/typing";

export interface State {
  VAImages: VAImage[];
  VAFeatures: VAFeatures[];
  selectedImages: string[];
  filterSelectedImages: string[];
  ifImageShow: {};
  ifGalleryImageShow: {};
  FeatureData : FeatureData[]; // 还要跟score 拼一下
  ViewOrgan: FeatureList[];
  ViewVariety: FeatureList[];
  ViewAmount: FeatureList[];
  PixelAmount: FeatureList[];
  PixelOrgan: FeatureList[];
  PixelVariety: FeatureList[];
  selectedFeature: string;

}


// define injection key
export const key: InjectionKey<Store<State>> = Symbol()

export type VAImage = {
  img: string;
  score: number;
  id: string;
  conference: string;
  year: number;
  title: string;
  award: string;
  prediction:number;
}

export type VAFeatures = {
  name: string;
  explanation: string;
}

export type FeatureData = {
  img: string;
  features: {};
  score: number;
  award: string;
}

export type FeatureList = {
  feature_name: string;
  explanation: string;
  significant: string;
}


// export type isImageShown = Map<string, boolean>

const VAImages = require('@/assets/alldata_update.json') as VAImage[];
const VAFeatures = require('@/assets/feature_with_explanation.json') as VAFeatures[];
const FeatureData = require('@/assets/full_feature_score.json') as FeatureData[];

export const ViewOrgan = require('@/assets/view_organ.json') as FeatureList[];
export const ViewVariety = require('@/assets/view_variety.json') as FeatureList[];
export const ViewAmount = require('@/assets/view_amount.json') as FeatureList[];
export const PixelAmount = require('@/assets/pixel_amount.json') as FeatureList[];
export const PixelOrgan = require('@/assets/pixel_organ.json') as FeatureList[];
export const PixelVariety = require('@/assets/pixel_variety.json') as FeatureList[];

var nullIsShown = {} as any;
VAImages.forEach(x => {
  nullIsShown[x.id] = true;
})

export const store = createStore<State>({
  state: {
    VAImages: VAImages,
    VAFeatures: VAFeatures,
    FeatureData: FeatureData,
    ViewOrgan: ViewOrgan,
    ViewVariety: ViewVariety,
    ViewAmount: ViewAmount,
    PixelAmount: PixelAmount,
    PixelOrgan: PixelOrgan,
    PixelVariety: PixelVariety,
    selectedImages: [],
    filterSelectedImages: [],
    ifImageShow: JSON.parse(JSON.stringify(nullIsShown)),
    ifGalleryImageShow: JSON.parse(JSON.stringify(nullIsShown)),
    selectedFeature: "",
  },
  modules: {
  },
  getters: {
  },
  mutations: {
    setSelectedImages(state, selectedImages: string[]) {
      state.selectedImages = selectedImages
      for (var key in nullIsShown){
        nullIsShown[key] = false;
      }

      var obj = {} as any;
      state.selectedImages.forEach(x => {
        obj[x] = true;
      })
      state.ifImageShow = {
        ...JSON.parse(JSON.stringify(nullIsShown)),
        ...obj
      }
    },
    setFilterSelectedImages(state, filterSelectedImages: string[]) {
      state.filterSelectedImages = filterSelectedImages
      for (var key in nullIsShown){
        nullIsShown[key] = false;
      }
      var filterObj = {} as any;
      state.filterSelectedImages.forEach(x => {
        filterObj[x] = true;
      })
      state.ifGalleryImageShow = {
        ...JSON.parse(JSON.stringify(nullIsShown)),
        ...filterObj
      }
    },
    setSelectedFeature(state,selectedFeature: string){
      state.selectedFeature = selectedFeature
    }
  },
  actions: {
    async init({ state, commit }) {
      // let data = (await dataService.getRuleList()).data;
      // commit('setRuleList', data);
    }
  },
  strict: process.env.NODE_ENV !== "production"
});

// define your own `useStore` composition function
export function useStore() {
  return baseUseStore(key)
};

